@import url(https://fonts.googleapis.com/css2?family=Asap:wght@400;500;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{
  font-family: 'Asap', sans-serif;
  background-color:  #eaeff4;
 }

 ::-webkit-input-placeholder {
  font-family: 'Asap', sans-serif;

 }

 :-ms-input-placeholder {
  font-family: 'Asap', sans-serif;

 }

 ::placeholder {
  font-family: 'Asap', sans-serif;

 }

.App {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  grid-gap: 15px;
  margin: 10px 30px;
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

legend {
  margin: 0 auto;
  font-size: 1.2rem;
  font-weight: 500;
  background-color: #00f0c4;
  padding: 5px 15px;
  color: #fff;
  border-radius: 3px;
 }

 fieldset {
   border-radius: 5px;
 }
.task-item {
  display: grid;
  border: 1px solid AliceBlue;
  margin: 5px auto;
  background-color: #fff;
  cursor: pointer;
  border-radius: 5px;
}

.remove {
  width: 1.5vw;
  height: 1.5vw;
  min-width: 19px;
  min-height: 19px;
  align-self: center;
  margin-right: 10px;
  border-radius: 50%;
  border: none;
  font-weight: bold;
  color: gray;
  cursor: pointer;
  font-size: 0.8rem;
  justify-self: end
}

.duration-display {
  margin-left: 10px;
}

.task-title {
  text-align: center;
}

.stack-list>.task-item {
  width: 100%;
  grid-template-columns: 1fr 3fr 1fr;
  line-height: 40px;
}

.heap-list>.task-item {
  width: 100%;
  grid-template-columns:  5fr 1fr;
  line-height: 30px;
}
.stack-container {
  grid-column: 1/4;
}

.stack-input {
  border: 2px solid #00f0c4;
  width: 100%;
  line-height: 40px;
  margin: 5px auto;
  position: relative;
  text-align: center;
  border-radius: 5px;
  font-size: 1.3rem;
  padding: 0;
}

.stack-list {
  display: flex;
  flex-direction: column-reverse;
  justify-content: center;
}

.stack-item {
  width: 100%;
}
.heap-container {
  grid-column: 1/7;
  display: flex;
  flex-direction: column;
}

.heap-list {
  display: grid;
  grid-template-columns: repeat(4,1fr);
  grid-gap: 5px 20px;
}

.heap-list>.task-item {
  width: 100%;
}

.heap-input {
  border: 1px solid #ccc;
  width: 70%;
  line-height: 1.8em;
  margin: 5px auto;
  position: relative;
  text-align: center;
  border-radius: 5px;
  font-size: 1rem;
  padding: 0;
}
.notes-container {
  grid-column: 4/7;
}

.notes-input {
  border: 1px solid #ccc;
  width: 100%;
  line-height: 1.8em;
  margin: 5px auto;
  position: relative;
  text-align: center;
  border-radius: 5px;
  font-size: 1rem;
  padding: 0;
}

.notes-container dt {
  font-weight: 600;
}

.notes-container dl {
  font-size: 0.9em;
}
.current-task-container {
  grid-column: 2/6;
  text-align: center;
  background-color: #fff;
  width: 80%;
  margin: 0 auto;
  border-radius: 5px;
  height: 95px;
  display: flex;
  flex-direction: column;
}

.current-title > h1 {
  margin: 0;
}

.current-timer {
  margin-top: 10px;
}

.current-timer > h4 {
  margin: 0;
}

.current-title {
  margin: 10px;
  position: relative;
  z-index: 3;
}

.current-task-background {
  display: flex;
  flex-flow: nowrap;
  width: 100%;
  height: 25%;
  position: relative;
  z-index: 0;

}

.time-bars {
  height: 100%;
  background-color: deeppink;
  margin: 0 3px;
  width: 10%;
  border-radius: 5px;
}
.global-timer {
  grid-column: 1/7;
  margin: 20px auto;
}
a {
  text-decoration: none;
  color: #fff;
}

.header {
  grid-column: 1/7;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header-left {
  width: 25%;
}

.header-center {
  width: 75%;
}

.instructions {
  text-align: center;
  width: 100%;
}

.header-right {
  display: flex;
  justify-content: flex-end;
  width: 25%;
  
  
}

.clear-btn, .end-meeting-btn{
  width: 50%;
  border: none;
  background-color: #828584;
  padding: 5px 15px;
  color: #fff;
  border-radius: 3px;
}
